}
.main_btn:before{content:'';position:absolute;top:5px;left:5px;bottom:5px;right:5px;background:#e72727;border:1px solid #ffffff;z-index:-1;-webkit-transition:all 300ms linear 0s;transition:all 300ms linear 0s
}
.main_btn:after{content:'';position:absolute;bottom:5px;right:5px;border-right:10px solid #ffffff;border-left:10px solid transparent;border-bottom:10px solid #ffffff;border-top:10px solid transparent;-webkit-transition:all 300ms linear 0s;transition:all 300ms linear 0s
}
.main_btn:hover{color:#262533
}
.main_btn:hover:before{background:#ffffff
}
.main_btn:hover:after{border-right:10px solid #e72727;border-bottom:10px solid #e72727
}
